*{
  border:none;
  box-sizing:border-box;
  margin:0;
  padding:0;
  font-size:20px;
  font-family: "DM Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
}

body{
  transition:.4s;
}

.div-main{
  height:100vh;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
}

.div-calculator{
  min-width:300px;
  max-width:300px;
  height:500px;
  background:#DDD;
  display:flex;
  flex-direction:column;
  border-radius:10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  
}

.display{
  height:21%;
  text-align:end;
  line-height:170px;
  padding:0px 25px;
  background:linear-gradient(90deg,#697A98,#19335A);
  color:#EEE;
  border-radius:10px 10px 0 0 ;
  margin-bottom:5px;  
  overflow:clip;
  transition:.4s;
}
.display-plus{
  background-image: linear-gradient(-20deg, #e9defa 0%, #fbfcdb 100%);
  color:#555;
}

.div-buttons{  
  border-radius: 0 0 10px 10px;
  height:79%;
  display:flex;
  background-image: linear-gradient(to top, #6a85b6 0%, #bac8e0 100%);
  transition:.4s;
  padding:5px 5px 5px 10px;
}
.div-buttons-plus{
  background-image: linear-gradient(to top, #B39CD0 0%, #FBEAFF 100%);
}

.div-left{
  width:70%;
  display:flex;
  flex-wrap:wrap;
  justify-content:center;
  align-items:center;
  column-gap:5px;
}
.div-right{
  width:30%;
  display:flex;
  flex-wrap:wrap;
  justify-content:center;
  align-items:center;
}

.button{
  color:#555;
  width:60px;
  height:60px;
  border-radius:50%;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.button:active{
  background:linear-gradient(90deg,#697A98,#19335A);
  color: #DDD;
}
.button-plus:active{
  background:linear-gradient(90deg,#FBEAFF,#B39CD0);
}

#clear{
  width:190px;
  border-radius:50px 50px;
}
#clear:active{
  background: linear-gradient(to right, #355c7d, #6c5b7b, #c06c84); 
}
#zero{
  
  width:130px;
  border-radius:50px 50px;
}

.sig{
  margin-top:15px;
  font-family: "Arya", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size:12px;
  color:#AFB3B7;
}
.sig-theme{
  color:#EEE;
}

.btnTheme{
  text-align:center;
  font-size:14px;
  color: #666;
  background:#FFF;
  width:80px;
  height:30px;
  line-height:23px;
  border-radius:4px;
  border:none;
  margin:18px;
  padding:3px 5px;  
  box-shadow: #DDD 1px 1px 2px 1px inset, #DDD -1px -1px 1px inset;
}
.btnTheme:active{
  color: #FFF;
  background:#CCC;
}


   /*home link*/
  #home{
    margin-left:-5px;
